.gallery {
    display: flex;
    width: 80%;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: space-between;
    padding-left: 0;
    list-style: none;

}

img {
    width: 240px;
    height: 160px;
}

.stats {
    display: flex;
    justify-content: space-between;
    font-size: 10px;
    margin-bottom: 15px;
}

.material-icons{
    font-size: 18px;
    display: flex;
    justify-content: center;

}

input {
    /* display: block; */
  padding: 10px 0;
  margin-top: 10px;
  margin-bottom: 30px;
   border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.747);

  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

body {
    
    background: #e2dbfc;
  
}

.more-btn {
   display: block;
   margin: 0 auto;
    
    box-shadow: 3px 4px 0px 0px #9fb4f2;
	background-color:#7892c2;
	border-radius:15px;
	border:1px solid #4e6096;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:7px 25px;
	text-decoration:none;
	text-shadow:0px 1px 0px #283966;
    margin-bottom: 15px;
}

.more-btn:hover{
    background-color:#476e9e;
}
